import { Cuti_kategori } from 'src/app/sdk/hr/models/cuti-_kategori'
import { KalenderCutiKategoriService } from 'src/app/sdk/hr/services'
import { Component, OnInit } from '@angular/core'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { Router, ActivatedRoute } from '@angular/router'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit-kategori',
    templateUrl: './edit-kategori.component.html',
    styleUrls: ['./edit-kategori.component.scss'],
})
export class EditKategoriComponent implements OnInit {
    constructor(
        private kalenderCutiKategoriService: KalenderCutiKategoriService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private router: Router,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Cuti_kategori

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_cuti_kategori')
        this.form()
        this.getData()
    }

    id: string
    getData() {
        this.kalenderCutiKategoriService.getKalenderCutiKategorisId(this.id).subscribe(data => {
            this.dataForm.setValue(data.data)
        })
    }

    dataForm: FormGroupTypeSafe<Cuti_kategori>
    form() {
        this.dataForm = this.fb.group<Cuti_kategori>({
            cuti_kategori: new FormControl('', [Validators.required]),
            id_cuti_kategori: new FormControl(''),
            is_lock: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.kalenderCutiKategoriService
            .putKalenderCutiKategorisId({
                id: this.id,
                body: this.data,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/mastercuti/list'])
    }
}
