import { Cuti_pengaturan } from 'src/app/sdk/hr/models/cuti-_pengaturan'
import { NgxSpinnerService } from 'ngx-spinner'
import { KalenderCutiPengaturanService } from 'src/app/sdk/hr/services'
import { Component, OnInit } from '@angular/core'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { Router, ActivatedRoute } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    pesan: any

    constructor(
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private fb: FormBuilderTypeSafe,
        private router: Router,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Cuti_pengaturan = {}

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_cuti_pengaturan')
        this.form()
        this.getData()
    }

    id: string = ''
    getData() {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturansId(this.id).subscribe(data => {
            this.dataForm.setValue(data.data)
        })
    }
    dataForm: FormGroupTypeSafe<Cuti_pengaturan>
    form() {
        this.dataForm = this.fb.group<Cuti_pengaturan>({
            kode: new FormControl('', [Validators.required]),
            nama_cuti: new FormControl('', [Validators.required]),
            perhitungan_cuti: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_user: new FormControl(''),
            saldo_cuti: new FormControl(''),
            if_tanggal_reload_cuti: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            id_cuti_pengaturan: new FormControl(''),
            id_cuti_kategori: new FormControl(''),
            minimal_gabung: new FormControl(''),
            id_aproval: new FormControl(''),
            pro_rata: new FormControl(''),
            reload_cuti: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.kalenderCutiPengaturanService
            .putKalenderCutiPengaturansId({
                id: this.id,
                body: this.data,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/mastercuti/list'])
    }
}
