import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'
import { KalenderCutiKategoriService } from 'src/app/sdk/hr/services/kalender-cuti-kategori.service'
import { Cuti_kategori } from 'src/app/sdk/hr/models/cuti-_kategori'
import { Component, OnInit } from '@angular/core'
import { KalenderCutiPengaturanService } from 'src/app/sdk/hr/services'

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
    constructor(
        private kalenderCutiKategoriService: KalenderCutiKategoriService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
    ) { }

    listCutiKategori: any[] = []
    ngOnInit() {
        this.getCutiKategori()
    }

    //   panggie kategori
    getCutiKategori() {
        this.kalenderCutiKategoriService.getKalenderCutiKategoris().subscribe(res => {
            this.listCutiKategori = res.data

            this.listCutiKategori.forEach((item, index) => {
                // panggie cuti prengaturan
                this.kalenderCutiPengaturanService
                    .getKalenderCutiPengaturans(
                        JSON.stringify({ id_cuti_kategori: item.id_cuti_kategori }),
                    )
                    .subscribe(dataRes => {
                        this.listCutiKategori[index]['pengaturan'] = dataRes.data
                    })
            })
        })
    }

    delete(id) {
        this.spinner.show()
        this.kalenderCutiPengaturanService.deleteKalenderCutiPengaturansId(id).subscribe(data => {
            this.spinner.hide()
        })
    }

    hapus(id) {
        this.spinner.show()
        this.kalenderCutiKategoriService.deleteKalenderCutiKategorisId(id).subscribe(data => {
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }
}
