import { Cuti_kategori } from 'src/app/sdk/hr/models/cuti-_kategori'
import { Component, OnInit } from '@angular/core'
import { KalenderCutiKategoriService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add-kategori',
    templateUrl: './add-kategori.component.html',
    styleUrls: ['./add-kategori.component.scss'],
})
export class AddKategoriComponent implements OnInit {
    constructor(
        private kalenderCutiKategoriService: KalenderCutiKategoriService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) { }

    data: Cuti_kategori

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Cuti_kategori>
    form() {
        this.dataForm = this.fb.group<Cuti_kategori>({
            cuti_kategori: new FormControl('', [Validators.required]),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.kalenderCutiKategoriService.postKalenderCutiKategoris(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/mastercuti/list'])
    }
}
