import { KalenderCutiPengaturanService, KalenderCutiKategoriService } from 'src/app/sdk/hr/services'
import { Component, OnInit } from '@angular/core'
import { Cuti_pengaturan, Cuti_kategori } from 'src/app/sdk/hr/models'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private kalenderCutiKategoriService: KalenderCutiKategoriService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Cuti_pengaturan
    id_cuti_kategori: string = null
    ngOnInit() {
        this.form()
        // this.getCutiKategori()
        this.activatedRoute.paramMap.subscribe(param => {
            if (param.get('id_cuti_kategori')) {
                console.log(param.get('id_cuti_kategori'))
                this.id_cuti_kategori = param.get('id_cuti_kategori')
                this.dataForm.get('id_cuti_kategori').setValue(this.id_cuti_kategori)
            }
        })
    }

    dataForm: FormGroupTypeSafe<Cuti_pengaturan>
    form() {
        this.dataForm = this.fb.group<Cuti_pengaturan>({
            nama_cuti: new FormControl('', [Validators.required]),
            kode: new FormControl('', [Validators.required]),
            perhitungan_cuti: new FormControl(''),
            reload_cuti: new FormControl(''),
            if_tanggal_reload_cuti: new FormControl(''),
            saldo_cuti: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            minimal_gabung: new FormControl(''),
            pro_rata: new FormControl(''),
            id_cuti_kategori: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.kalenderCutiPengaturanService.postKalenderCutiPengaturans(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/mastercuti/list'])
    }
}
