import { DetailPengaturanComponent } from './detail-pengaturan/detail-pengaturan.component'
import { AddKategoriComponent } from './add-kategori/add-kategori.component'
import { EditKategoriComponent } from './edit-kategori/edit-kategori.component'
import { AddComponent } from './add-pengaturan/add.component'
import { EditComponent } from './edit-pengaturan/edit.component'
import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { ListComponent } from './list-pengaturan/list.component'

const routes: Routes = [
    {
        path: '',
        redirectTo: 'list',
    },
    {
        path: 'list',
        component: ListComponent,
    },
    {
        path: 'edit/:id_cuti_pengaturan',
        component: EditComponent,
    },
    {
        path: 'add/:id_cuti_kategori',
        component: AddComponent,
    },
    {
        path: 'add-kategori',
        component: AddKategoriComponent,
    },
    {
        path: 'edit-kategori/:id_cuti_kategori',
        component: EditKategoriComponent,
    },
    {
        path: 'detail-pengaturan/:id_cuti_pengaturan',
        component: DetailPengaturanComponent,
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class MastercutiRoutingModule {}
